body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  color: #a2abbd;
  background: #21212b; }

.radio-container {
  width: 320px;
  max-height: 600px;
  border-radius: 20px;
  box-shadow: 0px 20px 20px 0px #14141a; }

.radio-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding: 35px;
  border-radius: 20px 20px 0 0;
  background: #edae61;
  color: white;
  font-weight: bold;
  text-transform: uppercase; }
  .radio-header-icon {
    cursor: pointer;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out; }
  .radio-header-icon:hover {
    -webkit-transform: scale(0.8);
            transform: scale(0.8); }

.status-msg {
  text-align: center;
  margin-top: 50%; }

.radio-stations {
  height: 350px;
  background: #2b2b36;
  overflow-y: auto;
  margin: 0;
  padding: 20px; }
  .radio-stations-list {
    list-style: none;
    margin: 0;
    padding: 0; }
    .radio-stations-list > *:not(:last-child) {
      border-bottom: 1px solid #40404a; }

/* ------------------------------*/
/*            FONTS              */
/* ------------------------------*/
/* ------------------------------*/
/*            VARIABLES          */
/* ------------------------------*/
/* ------------------------------*/
