.active-station {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 0; }
  .active-station-cover {
    width: 100px;
    height: 100px;
    border: 1px solid #a2abbd;
    border-radius: 100%;
    background-position: center;
    background-size: cover; }
  .active-station-volume {
    cursor: pointer;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transition: 0.15s;
    transition: 0.15s; }
  .active-station-volume:hover {
    -webkit-transform: scale(0.6);
            transform: scale(0.6); }

/* ------------------------------*/
/*            FONTS              */
/* ------------------------------*/
/* ------------------------------*/
/*            VARIABLES          */
/* ------------------------------*/
/* ------------------------------*/
/*            MIXINS             */
/* ------------------------------*/
.station {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  cursor: pointer; }
  .station-name {
    text-transform: capitalize; }
  .station-frequency {
    font-weight: bold; }

/* ------------------------------*/
/*            FONTS              */
