.currently-playing {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100px;
  padding: 20px;
  border-top: 1px solid #4d505c;
  border-radius: 0 0 20px 20px;
  background: #22222b;
  text-align: center; }
  .currently-playing-header {
    margin-bottom: 10px;
    color: #edae61;
    font-size: 0.7em;
    text-transform: uppercase;
    font-weight: bold; }

