@import './utilities.scss';

body {
	@include flex-center;
	height: 100vh;
	font-family: 'Roboto', sans-serif;
	font-size: 20px;
	color: $light-grey;
	background: #21212b;
}

.radio-container {
	width: 320px;
	max-height: 600px;
	border-radius: 20px;
	box-shadow: 0px 20px 20px 0px #14141a;
}

.radio-header {
	@include flex-space-between;
	height: 80px;
	padding: 35px;
	border-radius: 20px 20px 0 0;
	background: $orange;
	color: white;
	font-weight: bold;
	text-transform: uppercase;

	&-icon {
		cursor: pointer;
		transform: scale(0.6);
		transition: 0.2s ease-out;
	}

	&-icon:hover {
		transform: scale(0.8);
	}
}

.status-msg {
	text-align: center;
	margin-top: 50%;
}

.radio-stations {
	height: 350px;
	background: #2b2b36;
	overflow-y: auto;
	margin: 0;
	padding: 20px;

	&-list {
		list-style: none;
		margin: 0;
		padding: 0;

		& > *:not(:last-child) {
			border-bottom: 1px solid #40404a;
		}
	}
}
